import React, { Component } from "react";
import { Tabs, Tab, Grid } from "react-mdl";
import ProjectCard from "./projectCard";
import projectData from "./data/projects";

export default class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  getProjects(projectType) {
    const jsProject = projectData[projectType];
    var el = jsProject.map(data => (
      <ProjectCard key={data.projectTitle} {...data} />
    ));

    return <Grid>{el}</Grid>;
  }

  switchProjects(index) {
    switch (index) {
      case 0:
        return this.getProjects("js");
      case 1:
        return this.getProjects("android");
      case 2:
        return this.getProjects("others");
      default:
        break;
    }
    return <h1>Hello</h1>;
  }
  render() {
    return (
      <div className="category-tabs">
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>JavaScript</Tab>
          <Tab>Android</Tab>
          <Tab>Others</Tab>
        </Tabs>
        <section>
          <div className="category-grid">
            {this.switchProjects(this.state.activeTab)}
          </div>
        </section>
      </div>
    );
  }
}
