import React from "react";
import {
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  Button
} from "react-mdl";

export default function ProjectCard(props) {
  console.log(props);
  const titleStyle = {
    color: "#fff",
    background: "url(" + props.backGroundImg + ") center no-repeat #46B6AC",
    backgroundSize: "cover"
  };
  return (
    <Cell col={4} phone={12}>
      <Card
        shadow={0}
        style={{ minWidth: "320px", height: "320px", margin: "auto" }}
      >
        <CardTitle expand style={titleStyle}>
          {props.projectTitle}
        </CardTitle>
        <CardText>{props.projectDescription}</CardText>
        <CardActions border>
          {props.liveDemoHref && (
            <a href={props.liveDemoHref} target="liveDemo">
              <Button colored>Live Demo</Button>
            </a>
          )}
          {props.githubRepo && (
            <a href={props.githubRepo} target="github">
              <Button colored>Github Repo</Button>
            </a>
          )}
          {props.playStoreLink && (
            <a href={props.playStoreLink} target="playStore">
              <Button colored>Play Store</Button>
            </a>
          )}
        </CardActions>
      </Card>
    </Cell>
  );
}
